.banner {
  color: white;
  object-fit: contain;
  height: 650px;
}

.banner_contents {
  margin-left: 30px;
  padding-top: 140px;
  height: 190px;
}

.banner_title {
  margin-top: 200px;
  font-size: 3rem;
  font-weight: 800;
  padding-bottom: 0.3rem;
}

.banner__description {
  width: 45rem;
  line-height: 1.3;
  padding-top: 1rem;
  font-size: 1rem;
  max-width: 360px;
  height: 80px;
}

.banner__buttons {
  cursor: pointer;
  color: #fff;
  outline: none;
  border: none;
  font-weight: 700;
  border-radius: 0.2vw;
  padding-left: 2rem;
  padding-right: 2rem;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  background-color: rgba(51, 51, 51, 0.5);
  margin-left: 10px;
}

.banner__buttons:hover {
  color: #000;
  background-color: #e6e6e6;
  transition: all 0.2s;
}

.banner--fade {
  height: 26rem;
  background-image: linear-gradient(
    180deg,
    transparent,
    rgba(37, 37, 37, 0.7),
    #111
  );
}

.banner.loading .banner_contents {
  visibility: hidden;
}

.banner.loading .banner_title,
.banner.loading .banner__description,
.banner.loading .banner_buttons {
  background-color: #ddd;
  animation: loadingAnimation 1s infinite alternate;
}

@keyframes loadingAnimation {
  from {
    opacity: 0.7;
  }
  to {
    opacity: 1;
  }
}

@media (max-width: 768px) {
  .banner {
    height: 400px;
  }

  .banner_contents {
    padding-top: 100px;
    margin-left: 15px;
  }

  .banner_title {
    margin-top: 150px;
    font-size: 2rem;
  }

  .banner__description {
    max-width: 240px;
    padding-top: 0.5rem;
  }

  .banner__buttons {
    padding-left: 1rem;
    padding-right: 1rem;
    font-size: 0.8rem;
  }
}

@media (min-width: 768px) and (max-width: 1199px) {
  .banner {
    height: 500px;
  }

  .banner_contents {
    margin-left: 20px;
    padding-top: 100px;
  }

  .banner_title {
    margin-top: 150px;
    font-size: 2.5rem;
  }

  .banner__description {
    max-width: 240px;
  }

  .banner__buttons {
    padding-left: 1.5rem;
    padding-right: 1.5rem;
    font-size: 0.9rem;
  }
}

@media (max-width: 767px) {
  .banner {
    height: 400px;
  }

  .banner_contents {
    padding-top: 100px;
    margin-left: 15px;
  }

  .banner_title {
    margin-top: 150px;
    font-size: 2rem;
  }

  .banner__description {
    max-width: 240px;
    padding-top: 0.5rem;
  }

  .banner__buttons {
    padding-left: 1rem;
    padding-right: 1rem;
    font-size: 0.8rem;
  }
}