.banner {
  color: white;
  object-fit: contain;
  height: 650px;
  /* height: 448px; */
}

.banner_contents {
  margin-left: 30px;
  padding-top: 140px;
  height: 190px;
}

.banner_title {
  margin-top: 200px;
  font-size: 3rem;
  font-weight: 800;
  padding-bottom: 0.3rem;
}

.banner__description {
  width: 45rem;
  line-height: 1.3;
  padding-top: 1rem;
  font-size: 1rem;
  max-width: 360px;
  height: 80px;
}

.banner__buttons {
  cursor: pointer;
  color: #fff;
  outline: none;
  border: none;
  font-weight: 700;
  border-radius: 0.2vw;
  padding-left: 2rem;
  padding-right: 2rem;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  background-color: rgba(51, 51, 51, 0.5);
  margin-left: 10px;
}

.banner__buttons:hover {
  color: #000;
  background-color: #e6e6e6;
  transition: all 0.2s;
}

.banner--fade {
  height: 26rem;
  background-image: linear-gradient(
    180deg,
    transparent,
    rgba(37, 37, 37, 0.7),
    #111
  );
}

.trending_row {
  margin-top: 20px;
}

@media (max-width: 768px) {
  .banner_title {
    font-size: 2rem;
  }

  .banner__description {
    font-size: 0.9rem;
  }
}

@media (min-width: 768px) and (max-width: 1199px) {
  .banner_title {
    font-size: 2.5rem;
  }

  .banner__description {
    font-size: 0.9rem;
  }
}

@media (max-width: 767px) {
  .banner_title {
    font-size: 2rem;
  }

  .banner__description {
    max-width: 180px;
    padding-top: 0.5rem;
  }

  .banner__buttons {
    padding-left: 1rem;
    padding-right: 1rem;
    font-size: 0.8rem;
  }
}
