.nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 20px;
  background-color: #111;
  color: #fff;
}

.nav_black {
  background-color: #000;
}

.nav_logo img {
  cursor: pointer;
  height: 50px;
}

.menu {
  display: flex;
  gap: 20px;
  margin-right: 70%;
}

.nav__item {
  color: #fff;
  text-decoration: none;
  padding: 8px 12px;
}

.nav__item:hover,
.nav__item.nav__active {
  border-bottom: 2px solid #e50914;
}

/* Dropdown Styles */

.dropdown {
  position: relative;
}

.dropdown-content {
  display: none;
  position: absolute;
  background-color: #000;
  min-width: 120px;
  z-index: 1;
  top: 100%;
  right: 0;
}

.dropdown:hover .dropdown-content,
.open {
  display: block;
}

.dropdown-content span {
  color: white;
  padding: 10px;
  display: block;
  cursor: pointer;
  text-align: left;
}

.dropdown-content span:hover {
  background-color: #333;
}

.avatar {
  position: relative;
}

.avatar img {
  height: 50px;
  cursor: pointer;
}

.login {
  position: absolute;
  top: 100%;
  right: 0;
  display: none;
  background-color: #000;
  min-width: 120px;
  z-index: 2;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.avatar:hover .login,
.login.open {
  display: block;
}

.login span {
  color: white;
  padding: 10px;
  display: block;
  cursor: pointer;
  text-align: right;
}

.login span:hover {
  background-color: #333;
}

@media (max-width: 768px) {
  .nav {
    width: 100%;
    padding: 2px 0px;
  }
  .menu {
    margin-right: 0%;
  }

  .nav_logo img {
    height: 40px;
  }

  .avatar img {
    height: 40px;
  }

  .avtar-container {
    margin-left: auto;
    margin-right: 20px;
  }

  .nav__item {
    padding: 5px 10px;
    font-size: 14px;
  }
}

