@media (min-width: 769px) and (max-width: 1024px) {
  /* Adjustments for Medium Screens */
  .nav {
    padding: 8px 20px;
  }

  .nav_logo img {
    height: 45px;
  }

  .avatar img {
    height: 45px;
  }

  .nav__item {
    padding: 6px 12px;
    font-size: 15px;
  }
}

@media (min-width: 1025px) {
  /* Adjustments for Large Screens */
  .nav {
    padding: 10px 20px;
  }

  .nav_logo img {
    height: 50px;
  }

  .avatar img {
    height: 50px;
  }

  .nav__item {
    padding: 8px 14px;
    font-size: 16px;
  }
}
